import barrage from '../../components/barrage.vue';
import scrollList from './scroll.vue'
import {
	findAllDyn,
	getImgTemp,
	getComment,
	addComment,
	concern,
	like,
	getBullet,
	reply
} from '../../api/api.js';

export default {
	components: {
		barrage,
		scrollList
	},
	data() {
		return {
			dynInfo: '',
			commentInfo: '',
			bulletList: '',
			following: false,
			did: 0, // 日志id
			uid: 0, // 用户id
			cid: 0, // 评论用户id
			beStatus: false,
			commContent: '',
			commplaceholder: '為保證用戶隱私，只可以看自己的評論',
			addType: '', // 发送按钮请求不同接口
			array: ['取消關注', '屏蔽用戶', '舉報用戶'],
			pickIndex: 0,
			likeNumber: 0,
			showComment: false,
			loadingType: 5,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	methods: {
		// 日志详情
		findDyn(loadTime) {
			findAllDyn().then(data => {
				if(this.dynInfo.length == data.length){
					uni.showToast({
							title: '已是最新',
							duration: 2000
						});
				}
				this.dynInfo = data.splice(0,this.loadingType);
				console.log(data)
				
				// this.likeNumber = 0; //重置点赞次数
				// 第一个特殊，因为需要滑动后才能监听到swiperChange，所以第一个直接赋值
				if (loadTime == 1) {
					this.did = this.dynInfo[0].id;
					this.uid = this.dynInfo[0].uid;
				}
				// 获取弹幕
				getBullet(this.did).then(data => {
					this.bulletList = data.content;
				});
			});
			 setTimeout(function () {
				uni.hideLoading();
			}, 1000);
		},
		//下拉刷新
		loadFindDyn(){
			findAllDyn().then(data => {
				if(this.dynInfo.length == data.length){
					uni.showToast({
							title: '已是最新',
							duration: 2000
						});
						return
				}
				this.loadingType +=5
				this.dynInfo += data.splice(this.loadingType);
				console.log(data)
				})
		},
		// 返回
		goBack() {
			uni.switchTab({
				url: "../main/main"
			});
		},
		// 发表日志
		publishDyn() {
			getImgTemp().then(data => {
				this.$store.commit('setImgTemp', data);
				console.log(this.$store.state.imgTemp);
				uni.navigateTo({
					url: 'publish'
				});
			});
		},
		// 关注用户
		userFollow() {
			if (this.following == false) {
				concern(1, this.uid).then(data => {
					this.following = true;
					this.findDyn(2);
				});
			}
			// else this.following = false;
		},
		// 举报功能等实现
		bindPickerChange(e) {
			// console.log('picker发送选择改变，携带值为', e.target.value);
			this.pickIndex = e.target.value;
			if (e.target.value == 0) { //取消关注
				this.following = false;
				console.log(this.uid);
				concern(2, this.uid).then(data => {
					this.findDyn();
				});
			}
		},
		//
		behaviour() {
			if (this.beStatus == false) this.beStatus = true;
			else if (this.beStatus == true) this.beStatus = false;
			// console.log(this.beStatus);
		},
		// 用户详情
		userInfo() {
			uni.navigateTo({
				url: '/pages/daily/userInfo?uid=' + this.uid
			});
		},
		// 点赞
		likePerson(isOwn) {
			if (isOwn == 1) {
				uni.showToast({
					icon: 'none',
					title: '不可以給自己點贊哦'
				});
			} else {
				// like(this.did, 1);
				// this.findDyn(2);
				this.likeNumber++;  
				uni.showToast({
					icon: 'none',
					title: '點贊了' + this.likeNumber + '次'
				});
				// let likeTime = 5;
				// let timeCount = setInterval(() => {
				// 	likeTime --;
				// 	console.log('likeTime:' + likeTime);
				// 	if(likeTime < 1) {
				// 		clearInterval(timeCount);
				// 		console.log('likeNumber:' + this.likeNumber);
				// 		like(this.did, this.likeNumber);
				// 		this.findDyn(2);
				// 	}
				// }, 1000);
				like(this.did, this.likeNumber);
				this.findDyn(2);
			}
		},
		endLike() {
			uni.showToast({
				icon: 'none',
				title: '结束触摸，点击了' + this.likeNumber + '次'
			});
		},
		// 展示评论
		showComm() {
			(this.showComment == true) ? (this.showComment = false) : (this.showComment = true);
			getComment(this.did).then(data => {
				this.commentInfo = data;
				// console.log(this.commentInfo);
			});
		},
		// 获取swiper滚动后日志值
		swiperChange(e) {
			this.following = false; // 重置关注按钮状态
			this.showComment = false; // 重置评论样式
			this.addType = ''; // 重置发送按钮类型
			this.commplaceholder = '  為保證用戶隱私，只可以看自己的評論'; // 清除占位符
			this.commentInfo = '' // 清空评论

			// console.log(e.detail.current);
			this.did = this.dynInfo[e.detail.current].id;
			this.uid = this.dynInfo[e.detail.current].uid;
			// 获取弹幕
			getBullet(this.did).then(data => {
				this.bulletList = data.content;
			});
		},
		// 评论
		addComm() {
			// console.log(this.did);
			if (this.commContent != '') {
				if (this.addType == 'reply') {
					console.log('刚刚是回复');
					// console.log('cid' + this.cid);
					// console.log('commContent' + this.commContent);
					reply(this.cid, this.commContent).then(data => {
						this.findDyn(2);
						this.addType = ''; // 重置发送按钮类型
						this.commplaceholder = '  為保證用戶隱私，只可以看自己的評論'; // 清除占位符
					});
				} else {
					console.log('刚刚是評論');
					addComment(this.commContent, this.did).then(data => {
						getComment(this.did).then(data => {
							this.commentInfo = data;
							this.commContent = '';
							// console.log(this.commentInfo);
						});
						// 获取弹幕
						getBullet(this.did).then(data => {
							this.bulletList = data.content;
						});
					});
				}
			}
		},
		// 回复评论
		replyComm(cid, name, comUid) {
			if (comUid != uni.getStorageSync('USERS_KEY').id) {
				this.commplaceholder = '@' + name;
				this.addType = 'reply';
				this.cid = cid;
			} else {
				uni.showToast({
					icon: 'none',
					title: '不能自己回復自己哦'
				})
			}
		}
	},
	// onLoad() {
	// 	uni.showLoading({
	// 		title: '加载中',
	// 		success() {
	// 		this.findDyn(1); 
	// 		}
	// 	});
	// 	
	// },
	onLoad() {
		uni.showLoading({
			title: '加载中',
		});
		this.findDyn(1); 
	},
	onPullDownRefresh() { //监听下拉刷新动作
		console.log('onPullDownRefresh');
		// 这里获取数据
		this.findDyn(1);
		setTimeout(function() {
			//初始化数据
			uni.stopPullDownRefresh(); //停止下拉刷新
		}, 1000);
	},
	onPullDownRefresh() {
		this.loadFindDyn();
	},
}
